/*--------------------------------------------------------------
# Module - Intro Box
--------------------------------------------------------------*/

#intro-box {
	padding: 80px 0;
	margin-bottom: 4em;

	h2 {
		font-family: 'Nothing You Could Do', serif!important;
		color: #c37829;
		font-weight: 100;
		font-size: 3rem;
		margin: 0 auto 30px;

		@include breakpoint( medium down) {
			font-size: 2rem;
		}
		@include breakpoint( small down) {
			font-size: 1.75rem;
		}
	}

	.intro-box-text {
		color: #434343;
		font-weight: 400;
		font-size: 1.125rem;
		margin-bottom: 30px;
		@include breakpoint( medium down) {
			font-size: 1rem;
		}
		@include breakpoint( small down) {
			font-size: 0.9375rem;
		}
	}

	.continue-reading a {
		background-color: #000;
		font-size: .75em;
	}

	.continue-reading a:hover {
		background-color: #c99d6e;
	}
}


/*--------------------------------------------------------------
# Module - Promo Boxes
--------------------------------------------------------------*/
#promo-boxes {
	padding: 0;
	margin-bottom: 4em;

	.promo-box-intro{
		margin-bottom: 20px;
	}

	.promo-box-img {
		width: 100%;
		height: auto;
	}

	.promo-box {
		@include breakpoint( small down) {
			margin-bottom: 40px;
		}
	}

	.promo-box-content {
		text-align: center;
		padding: 20px 0;
		background-color: #000;
		.title {
			color: #c99d6e;
			font-family: 'Nothing You Could Do', serif;
			font-size: 19px;
			margin-bottom: 7px;
		}

		.action {
			font-family: 'Overpass', sans-serif;
			text-transform: uppercase;
			font-weight: 600;
			letter-spacing: 0.15em;
			font-size: 0.875rem;
			margin-top: 5px;
			@include breakpoint( medium down) {
				font-size: 0.75rem;
			}

			a {
				color: #fff;
				&:hover {
					text-decoration: underline;
				}
			}
		}
	}
}
