
/*--------------------------------------------------------------
# Articles
--------------------------------------------------------------*/

#primary .hentry,
#primary #first-post .hentry {
	margin: 0 0 2.35em;
	border-bottom: 2px solid #f3f2f1;
	padding-bottom: 2em;
}

#primary .hentry:last-of-type {
	margin: 0;
	padding-bottom: 0;
	border-bottom: none;
}

.site-main .hentry { clear: both; }

.post-main {
	overflow: hidden;
	padding: 0 15px 10px;
}

@media screen and (min-width: 62em) {
	.has-post-thumbnail .post-main {
		padding: .7em 2.2em;
	}

	.post-main {
		padding: 0 2.2em 0 0;
	}
}

@media screen and (min-width: 62em) {
	.large-12 .post-main {
		padding: .7em 4.2em;
	}
}

/* ## Article Thumbnail */

.post-thumb,
.post-thumb-mobile {
	margin-bottom: 25px;
}

@media screen and (min-width: 62em) {
	.post-thumb {
		float: left;
		margin-right: 25px;
		margin-bottom: 0;
		width: 45%;
	}
}

@media screen and (min-width: 69em) {
	.post-thumb {
		width: 42%;
	}
	.large-12 .post-thumb {
		width: 26%;
	}
}

.site-main .post-thumb img {
	width: 100%;
}

/* ## Article Category */

.entry-category {
	display: block;
	color: #d47d22;
	font-size: 21px;
	font-family: 'Nothing You Could Do', serif;
	text-decoration: none;
	line-height: 32px;
	font-weight: 500;
}

.single .entry-category {
	font-size: 26px;
}


.entry-category a {
	color: inherit;
}

/* ## Article Title */

.entry-title {
	color: #000;
	font-size: 32px;
	font-weight: 500;
	line-height: 44px;
	text-decoration: none;
	padding: 0;
	transition: all .3s;
	a {
		color: #000!important;
		padding: 7px 0;

		&:hover,
		&:active,
		&:focus {
			color: #999!important;
			outline: none;
		}
	}

}


/* ## Article Meta Content */

.entry-meta {
	display: block;
	color: #959595!important;
	font-family: 'Lato', sans-serif;
	font-size: 14px;
	font-weight: 400;
	margin-bottom: 20px;

	.byline {
		margin-right: 1em;
	}

	a {
		color: #959595!important;
		font-family: 'Lato', sans-serif;
		font-weight: 400;
		margin-left: 4px;
	}

}


/* ## Article Excerpt */

.entry-content {
	margin: 0;
	text-align: left;
}


.entry-content li {
	margin: 2px 0 2px 40px;
	padding: 2px 0 2px 0px;
}


.entry-content p {
	color: #434343;
	font-weight: 400;
	font-size: 15px;
	margin: .4em auto 2em;
}

.entry-content a:hover,
.entry-content a:focus,
.entry-content a:active,
.posted-on a:hover,
.posted-on a:focus,
.posted-on a:active,
.byline a:hover,
.byline a:focus,
.byline a:active {
	border-bottom: 2px solid #dddddd;
}

.entry-content blockquote p,
.comment-content blockquote pg35 {
	line-height: 1.3em;
	font-family: 'Overpass', sans-serif;
	font-size: 1.2em;
	color: #000;
	font-weight: 700;
	margin: 0 !important;
}

/* ## Article Continue Reading */


.continue-reading a {
	color: #fff!important;
	background-color: #c99d6e;
	border-radius: 99px;
	font-family: 'Overpass', sans-serif;
	text-decoration: none;
	letter-spacing: 1px;
	font-size: 12px;
	padding: 17px 22px 15px;
	transition: all .6s;
	line-height: 46px;
}

.continue-reading a:hover,
.continue-reading a:active,
.continue-reading a:focus {
	background-color: #000!important;
	color: #fff;
}

.arrow {
	margin-left: 10px;
	opacity: .7;
	font-size: 1.2em;
}

/* ## Article Footer */

.entry-footer {
	background-color: inherit;
	margin-top: inherit;
	padding: inherit;
}

.entry-footer span {
	display: block;
	line-height: 3em;
	font-size: .9em;
	font-family: 'Overpass', sans-serif;
	font-weight: 500;
}

span.tags-links {
	border-top: 2px solid #f3f2f1;
	color: #000;
	font-family: 'Overpass', sans-serif;
	font-weight: 600;
	padding: 1.5em 0;
	margin: 0;
}

.tags-links a,
.tags-links a:visited {
	padding: 11px 14px 8px;
	color: #fff;
	border-radius: 99px;
	background-color: #c99d6e;
	text-transform: uppercase;
	margin-left: 1em;
	font-weight: 600;
	text-decoration: none;
	letter-spacing: 0.15em;
	font-size: .65em;
	transition: all .3s;
	font-family: 'Overpass', sans-serif;
	transition: all .6s;
	display: inline-block;
	line-height: 1;
}

.tags-links a:hover,
.tags-links a:active,
.tags-links a:focus {
	color: #fff;
	background-color: #000;
}

.edit-link a {
	padding: .6em .9em;
	border: 1px solid #ebebeb;
	transition: all .3s;
	font-family: 'Overpass', sans-serif;
	font-weight: 600;
}

.edit-link a:hover,
.edit-link a:focus,
.edit-link a:active {
	color: #fff;
	background-color: #999;
	border: 1px solid #999;
	transition: all .3s;
}

