.grid-container.fluid {
	width: 90%;

	@include breakpoint( medium down) {
		width: 94%;
	}

	@include breakpoint( small down) {
		width: 100%;
	}

}

.desktop-search {
	max-width: 200px;
	margin-top: 20px;
	float: right;
}
/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/

.is-boxed {
	background-color: #f4f4f4 !important;
}

body.custom-background {
	background-image: none !important;
}

.site-header {
	position: fixed;
	background-size: cover;
	width: 100%;
	background-color: #fff;
	top: 0;
	transition: top ease-out 300ms;
	z-index: 99;
}

@media screen and (min-width: 62em) {
	.site-header {
		position: inherit;
		margin-bottom: 0;
	}
}

.site-header.hide {
	top: -5.1em;
	transition: top ease-in 300ms;
}

@media screen and (min-width: 62em) {
	.site-header.hide {
		top: 0;
	}
}

.menu-active.hide {
	top: 0;
}



#desktop-site-navigation {
	&.header-2-nav {
		text-align: right;
		border: none;
	}
}


/* ## Header Top Bar */

#top-bar {
	background-color: #000;

	@include breakpoint( medium down) {
		display: none;
	}

	#social-header {
		padding: 10px 0;
		margin: 0;
		li {
			list-style: none;
			float: left;
			margin-right: 10px;
			a {
				font-size: 0.8rem;
			}
		}
	}


	#top-bar-navigation {

		font-weight: 700;

		.toggled-on {
			top: 40px;
		}

		.sub-menu.toggled-on ul {
			border: none;
			top: inherit;
			padding-top: 0;
			max-width: 100%;
		}

		a {
			font-size: 11px;
			letter-spacing: 0.08em;
			display: block;
			font-family: 'Lato', sans-serif;
			font-weight: 600;
			text-transform: uppercase;
			padding: 13px 0;
			position: relative;
			letter-spacing: 3px;
		}
	}

	.search-form {
		opacity: 1;
		display: block;
		z-index: 1;
		-webkit-box-shadow: none;
		-moz-box-shadow: none;
		box-shadow: none;
		padding: 0 1em;
		border-radius: 5px;
		background: rgba(white, 0.1);

		input {
			margin: 0px;
			padding: 20px;
			background: none;
			width: 100%;
			border: none;
			font-family: 'Lato', sans-serif;
			font-style: italic;
			font-weight: 300;
			color: #fff;
		}
		input:focus {
			box-shadow: none;
			border: none;
			outline: none;
			color: #fff;
		}

		.search-icon {
			display: none;
		}

		#searchsubmit {
			display: none;
		}
	}

}



/* Header Menu*/
.site-header-wrapper {
	padding: 40px 0;
	position: relative;
	opacity: 0;
	transition: all .3s;

	@include breakpoint( medium down) {
		padding: 20px 0;
	}

	.follow-us-text {
		font-family: 'Nothing You Could Do', serif;
		font-size: 1.4em;
		margin-top: 20px;

		@include breakpoint( medium down) {
			display: none;
		}
	}

	.social-header {
		float: left;
		margin: 0px;
		padding: 0px;
		li {
			list-style: none;
			display: inline-block;
			margin-right: 10px;
		}

		@include breakpoint( medium down) {
			display: none;
		}
	}


	/* logo */
	.site-title {
		color: #000;
		font-family: 'Overpass', sans-serif;
		font-weight: 600;
		letter-spacing: .05em;
		font-size: 54px;
		line-height: 38px;
		margin: 0 0 20px 0;
		text-align: center;
		clear: inherit;
		transition: all .3s;
		a {
			text-decoration: none;
			color: inherit;
		}

		@include breakpoint(medium down) {
			font-size: 42px;
		}

		@include breakpoint(small down) {
			font-size: 24px;
		}
	}



	.site-description {
		font-size: 26px;
		line-height: 35px;
		color: #d47d22;
		margin: 0;
		font-family: 'Nothing You Could Do', serif;
		display: block;

		@include breakpoint( medium down) {
			display: none;
		}
	}


	.custom-logo {
		width: 90%;
		max-width: 330px;
		height: auto;
		margin-bottom: 20px;
		@include breakpoint( medium down) {
			margin-bottom: 0;
		}
	}

	.custom-logo-link {
		line-height: 0;
		display: block;
		transition: all .3s;
	}

	.hide-logo {
		display: none;
	}

	@media screen and (min-width: 90em) {
		.custom-logo-link img {
			width: inherit;
		}
	}

	.search-form {
		opacity: 1;
		display: block;
		z-index: 1;
		-webkit-box-shadow: none;
		-moz-box-shadow: none;
		box-shadow: none;
		padding: 0 1em;
		border-radius: 5px;
		background: #f4f4f4;

		@include breakpoint( medium down) {
			display: none;
		}

		.search-form-inner {
			position: relative;
				&::before {
				font-family: $fa;
				font-weight: 900;
				content: '\f002';
				position: absolute;
				top: 12px;
				right: 0px;

				@include breakpoint( medium down) {
					display: none;
				}
			}
		}

		input {
			margin: 0px;
			padding: 25px 0;
			background: none;
			width: 100%;
			border: none;
			font-family: 'Lato', sans-serif;
			font-style: italic;
			font-weight: 300;
			color: #fff;
			box-shadow: none;
		}
		input:focus {
			box-shadow: none;
			border: none;
			outline: none;
			color: #fff;
		}

		#searchsubmit {
			display: none;
		}
	}
}
