.home.has-slider #page {
	margin-top: 0;
}

@media screen and (min-width: 40em) {
	#primary {
		margin-bottom: 3em;
	}
}

/*--------------------------------------------------------------
# Pagination
--------------------------------------------------------------*/

.pagination, .page-links {
	font-size: 13px;
	font-family: 'Overpass', sans-serif;
	font-weight: 300;
	line-height: 35px;
	margin-top: 50px;
}

.page-links a {
	margin-right: .7em;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

.pagination ul {
	margin: 0;
	padding-left: 0;
	list-style: none;
}

.pagination ul li {
	float: left;
	margin-right: .6em;
}

.pagination ul li:last-of-type {
	margin-right: 0;
}

.pagination ul li a, .page-links a {
	padding: 1em 1.2em;
	border: 1px solid #ebebeb;
	transition: all .3s;
	color: #333;
}

.pagination ul li.active a,
.pagination ul li a:hover,
.pagination ul li a:focus,
.pagination ul li a:active,
.page-links a:hover,
.page-links a:focus,
.page-links a:active {
	background-color: #c99d6e;
	border: 1px solid #c99d6e;
	color: #fff;
}

.masonry .pagination, .masonry .page-links {
	margin-top: 0;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/

.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

/*--------------------------------------------------------------
# Captions
--------------------------------------------------------------*/

.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption-text {
	font-family: 'Overpass', sans-serif;
	font-size: .8em;
	background-color: #fff;
	padding: 1.3em;
	font-style: italic;
	line-height: 1.6em;
}

/*--------------------------------------------------------------
# Images
--------------------------------------------------------------*/

img.aligncenter {
	margin-top: 2em;
	margin-bottom: 2em;
}

img.alignright,
img.alignleft {
	margin-bottom: 2em;
	margin-top: 2em;
}

.centered-image {
	max-width: calc(100% + 3.2em);
	margin: 1.5em -1.6em;
}

@media screen and (min-width: 30em) {
	.centered-image {
		max-width: calc(100% + 4em);
		margin: 1.5em -2em;
	}

}

.featured-image {
	margin: 0;
}

@media screen and (min-width: 50em) {
		.centered-image {
		max-width: calc(100% + 6em);
		margin-right: -3em;
		margin-left: -3em;
	}
}


/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/

.alignleft {
	display: inline;
	float: left;
	margin-right: 3em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 3em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

