.site-main .posts-navigation,
.site-main .post-navigation {
	margin: 0 0 1.5em;
	overflow: hidden;
}

.post-navigation {
	font-family: 'Overpass', sans-serif;
	font-weight: 600;
	text-align: left;
	overflow: hidden;
}

.post-navigation a {
	color: #000;
	display: block;
	width: 100%;
	text-decoration: none;
	padding: 1.2em;
	border: 1px solid #ebebeb;
	transition: all .3s;
	background-color: #fbfbfb;
}

@media screen and (min-width: 62em) {
	.post-navigation a {
		padding: 1.5em;
	}
}

@media screen and (min-width: 80em) {
	.post-navigation a {
		padding: 1.8em;
	}
}

.post-navigation a:hover,
.post-navigation a:focus {
	border-color: #c99d6e;
	color: #fff;
	background-color: #c99d6e;
}

.post-navigation .post-title {
	font-size: 1em;
	font-weight: 700;
}

.nav-previous {
	padding-bottom: 1em;
	width: 100%
}

.nav-next {
	width: 100%;
}

@media screen and (min-width: 30em) {
	.nav-links {
		display: flex;
		justify-content: space-between;
		align-items: stretch;
		padding: 0;
	}

	.post-navigation .nav-previous,
	.comment-navigation .nav-previous {
		display: flex;
		align-items: stretch;
		float: left;
		width: 48%;
	}

	.nav-previous {
		padding-bottom: 0;
	}

	.post-navigation .nav-next,
	.comment-navigation .nav-next {
		display: flex;
		align-items: stretch;
		float: right;
		text-align: right;
		width: 48%;
	}
}

.post-navigation .meta-nav {
	font-family: 'Overpass', sans-serif;
	display: block;
	font-weight: 400;
	font-size: .9em;
	margin-bottom: .6em;
}

@media screen and (min-width: 62em) {
	.post-navigation .meta-nav {
		padding-bottom: 0;
	}
}


/*--------------------------------------------------------------
# Module - Newsletter Box
--------------------------------------------------------------*/

#newsletter {
	margin-bottom: 3.5em;
}

.row.newsletter-wrapper {
	max-width: 87rem;
}

.newsletter-inner {
	background-position: center;
	background-size: cover;
	padding: 2em 1.5rem;
	background-color: #999;
}

.wysija-paragraph,
.mc4wp-form-fields p {
	position: relative;
	margin: 0;
}

#newsletter .allmsgs {
	border: 0;
	margin-bottom: 10px;
	background: inherit;
	color: #fff;
	text-align: center;
	padding: 1.4em;
	font-family: 'Overpass', sans-serif;
}

.newsletter-text {
	text-align: center;
}

#newsletter h2 {
	color: #fff;
	margin: 0;
	font-weight: 700;
	font-size: 2.4em;
	font-size: 1.7em;
}

@media screen and (min-width: 62em) {
	#newsletter h2 {
		font-size: 1.6em;
		line-height: 1.7em;
	}
}

@media screen and (min-width: 80em) {
	#newsletter h2 {
		font-size: 1.9em;
		line-height: 1.6em;
	}
}

#newsletter .newsletter-heading-2 {
	color: #fff;
	margin: .8em 0;
	font-size: .8em;
	letter-spacing: 0.06em;
	line-height: 1.8em;
	display: block;
}

@media screen and (min-width: 62em) {
	#newsletter .newsletter-heading-2 {
		margin: 0;
		font-size: .9em;
	}
}

.newsletter-form {
	margin-top: .55em;
}

#newsletter input[type="text"],
#newsletter input[type="email"] {
	float: left;
	padding: 1.1em;
	background-color: inherit;
	border: 2px solid rgba(255,255,255,0.25);
	margin-right: 1.5em;
	color: rgba(255,255,255,0.8);
	font-style: italic;
	font-size: .85em;
	font-weight: 300;
	text-align: center;
	width: 100%;
	margin-bottom: 1em;
}

#newsletter .mc4wp-form-fields input[type="email"] {
	width: 100%;
}


@media screen and (min-width: 64em) {
	#newsletter .mc4wp-form-fields input[type="email"] {
		width: 76%;
	}
}

@media screen and (min-width: 72em) {
	#newsletter .mc4wp-form-fields input[type="email"] {
		width: 78%;
	}
}

@media screen and (min-width: 80em) {
	#newsletter .mc4wp-form-fields input[type="email"] {
		width: 81%;
	}
}

#newsletter .mc4wp-form-fields ::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: rgba(255,255,255,0.8);
}
#newsletter .mc4wp-form-fields ::-moz-placeholder { /* Firefox 19+ */
  color: rgba(255,255,255,0.8);
}
#newsletter .mc4wp-form-fields :-ms-input-placeholder { /* IE 10+ */
  color: rgba(255,255,255,0.8);
}
#newsletter .mc4wp-form-fields :-moz-placeholder { /* Firefox 18- */
  color: rgba(255,255,255,0.8);
}

.mc4wp-alert p {
	color: #fff;
	float: left;
	margin: 0;
	font-weight: 300;
}

#secondary .mc4wp-alert p {
	margin-top: 2em;
	font-size: .8em;
	float: inherit;
}

@media screen and (min-width: 64em) {
	#newsletter input[type="text"],
	#newsletter input[type="email"] {
		width: 31%;
	}
}

@media screen and (min-width: 72em) {
	#newsletter input[type="text"],
	#newsletter input[type="email"] {
		width: 35%;
	}
}

@media screen and (min-width: 80em) {
	#newsletter input[type="text"],
	#newsletter input[type="email"] {
		width: 36%;
	}
}

#newsletter input[type="text"]:focus,
#newsletter input[type="text"]:active,
#newsletter input[type="email"]:focus,
#newsletter input[type="email"]:active {
	outline: none;
}

#newsletter input[type="submit"] {
	background-color: #000;
	float: left;
	text-transform: uppercase;
	font-size: .7em;
	padding: 1.88em 2.3em;
	font-weight: 500;
	margin-top: 0;
	letter-spacing: 0.2em;
	width: 100%;
}

@media screen and (min-width: 64em) {
	#newsletter input[type="submit"],
	#newsletter input[type="email"] {
		width: inherit;
	}
}

#newsletter label {
	display: none;
}

/*--------------------------------------------------------------
# Sidebar
--------------------------------------------------------------*/

#secondary {
	margin-bottom: 2em;
	position: relative;
}

@media screen and (min-width: 40em) {
	#secondary {
		margin-bottom: 3em;
	}
}

@media screen and (min-width: 64em) {
	#secondary {
		margin-top: 0;
	}
}

#secondary.left-sidebar {
	margin-top: 0;
}


/*--------------------------------------------------------------
# Plugins
--------------------------------------------------------------*/

.entry-meta-content .addtoany_content_top {
	margin: .7em 0;
}

@media screen and (min-width: 35em) {
	.entry-meta-content .addtoany_content_top {
		float: left;
		margin: 0 2em 0 0;
	}
}

.addtoany_list a > span {
	border-radius: 99px !important;
	background-color: #c99d6e;
	opacity: 1 !important;
	padding: 5px;
	box-sizing: content-box;
	transition: all .3s;
}

@media screen and (min-width: 40em) {
	.addtoany_list a > span {
		padding: 9px;
	}
}

.addtoany_list a > span:hover,
.addtoany_list a > span:focus,
.addtoany_list a > span:active {
	background-color: #000 !important;
}

.addtoany_list a {
	border: none !important;
	padding: 0 5px !important;
}

.woocommerce-tabs .addtoany_content_top {
	display: none;
}

.wpusb-icon-pinterest-default {
	padding: .8em .93em !important;
}

.wpusb .wpusb-item .wpusb-btn i {
	color: #fff !important;
	font-size: 15px;
	background-color: #000;
	border-radius: 99px;
	padding: .8em;
	transition: all .3s;
}

.wpusb .wpusb-item:last-of-type {
	margin-right: 2em;
}

.wpusb .wpusb-item .wpusb-btn i:hover {
	color: #fff;
	font-size: 15px;
	background-color: #999;
}


div.wpcf7 {
	padding: 1.5em 2.5em;
	background-color: #fbfbfb;
	border: 1px solid #ebebeb;
	margin: 2em 0;
}

.wpcf7 label {
	font-family: 'Overpass', sans-serif;
	font-size: .9em;
	line-height: 2.5em;
}

.wpcf7 textarea,
.wpcf7 input {
	border-radius: 0;
	border: 1px solid #e3e3e3;
	font-family: 'Overpass', sans-serif;
	font-size: 85%;
	padding: .7em;
}

.wpcf7 input[type="submit"] {
	background-color: #000;
	font-family: 'Overpass', sans-serif;
	font-weight: 500;
	border: none;
	color: #fff;
	border-radius: 0;
	padding: 1em 1.5em;
}

.wpcf7 input[type="submit"]:hover,
.wpcf7 input[type="submit"]:active,
.wpcf7 input[type="submit"]:focus {
	background-color: #999;
}

.woocommerce #primary {
	margin-bottom: 0;
}

@media screen and (min-width: 62em) {
	.woocommerce #primary {
		margin-bottom: 2em;
	}
}

.woocommerce {
	font-family: 'Overpass', sans-serif;
}
