/*--------------------------------------------------------------
## Slick Slider
--------------------------------------------------------------*/

.slick-slider {
	position: relative;
	display: block;
	box-sizing: border-box;
	-webkit-user-select: none;
	   -moz-user-select: none;
		-ms-user-select: none;
			user-select: none;

	-webkit-touch-callout: none;
	-khtml-user-select: none;
	-ms-touch-action: pan-y;
		touch-action: pan-y;
	-webkit-tap-highlight-color: transparent;
}

.slick-slider .slick-track,
.slick-slider .slick-list
{
	-webkit-transform: translate3d(0, 0, 0);
	   -moz-transform: translate3d(0, 0, 0);
		-ms-transform: translate3d(0, 0, 0);
		 -o-transform: translate3d(0, 0, 0);
			transform: translate3d(0, 0, 0);
}

.slick-list
{
	position: relative;
	display: block;
	overflow: hidden;
	margin: 0;
	padding: 0;
}

.slick-list:focus
{
	outline: none;
}

.slick-list.dragging
{
	cursor: pointer;
	cursor: hand;
}

.slick-track {
	position: relative;
	top: 0;
	left: 0;
	display: block;
}

.slick-track:before,
.slick-track:after {
	display: table;
	content: '';
}

.slick-track:after {
	clear: both;
}

.slick-loading .slick-track {
	visibility: hidden;
}

.slick-slide {
	display: none;
	float: left;
	outline: none;
	height: 100%;
	min-height: 1px;
}

[dir='rtl'] .slick-slide {
	float: right;
}

.slick-slide img {
	display: block;
}

.slick-slide.slick-loading img {
	display: none;
}

.slick-slide.dragging img {
	pointer-events: none;
}

.slick-initialized .slick-slide {
	display: block;
}

.slick-loading .slick-slide {
	visibility: hidden;
}

.slick-vertical .slick-slide {
	display: block;
	height: auto;
	border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
	display: none;
}

#slider-1 .slick-arrow {
	opacity: 0;
	transition: all .3s;
	background-color: #000;
	transform: scale(.7,.7);
}


#slider-1 .slick-arrow:hover {
	background-color: #999;
}

@media screen and (min-width: 40em) {
	#slider-1 .slick-arrow  {
		opacity: 0;
	}
	#slider-1 .slider-content:hover > .slick-arrow  {
		opacity: 1;
		transform: scale(1,1);
	}
 }

.slick-prev, .slick-next {
	z-index: 999;
	font-size: 0;
	position: absolute;
	top: 46%;
	width: 49px;
	height: 49px;
	cursor: pointer;
	border: none;
	border-radius: 100%!important;
	outline: none;
}

.slick-prev:before, .slick-next:before {
	font-family: 'Material Icons';
	font-size: 20px;
	line-height: 1;
	color: #fff;
}

.slick-prev::before {
	content: "\E314";
}

.slick-next::before {
	content: "\E315";
}

.slick-dots {
	text-align: center;
	margin: 1em 1em 0 0;
	padding: 0;
}

.slick-dots li {
	position: relative;
	display: inline-block;
	width: 37px;
	height: 16px;
	margin: 0;
	padding: 0;
	cursor: pointer;
}

.slick-dots li button {
	font-size: 0;
	line-height: 0;
	display: block;
	font-family: 'Material Icons';
	content: "";
	width: 37px;
	height: 16px;
	padding: 5px;
	cursor: pointer;
	color: transparent;
	border: 0;
	outline: none;
	background: transparent;
}

.slick-dots li button:hover,
.slick-dots li button:focus {
	outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
	opacity: 1;
}
.slick-dots li button:before {
	font-size: 48px;
	line-height: 20px;
	position: absolute;
	top: 0;
	left: 0;
	width: 40px;
	height: 20px;
	content: '\E15B';
	text-align: center;
	opacity: 1;
	color: #dcdcdc;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
	opacity: 1;
	color: #c99d6e;
}

/*--------------------------------------------------------------
## Slider 1
--------------------------------------------------------------*/

#slider-1 {
	margin-top: 0px;
	margin-bottom: 2em;
}
@include breakpoint(small only) {
	#slider-1 {
		margin: 50px 0 0 0;
	}
}


#slider-1 .slider-content {
	opacity: 0;
	margin-bottom: 0;
}

#slider-1 .slider-content .post-main {
	z-index: 99;
	position: relative;
	overflow: hidden;
}

@media screen and (min-width: 62em) {
	#slider-1 .post-main {
		padding: 1.2em;
		padding-right: 0;
		padding-left: 1px;
	}
}

@media screen and (min-width: 62em) {
	#slider-1 .entry-content p {
		font-size: 15px;
		line-height: 28px;
		width: 45em;
		color: #fff;
	}
}

#slider-1 .hentry {
	border-bottom: none;
	padding: 3em 1.5em;
	background-position: center;
	background-size: cover;
}

@media screen and (min-width: 62em) {
	#slider-1 .hentry {
		padding: 6em 3em;
		background-position: center;
		background-size: cover;
	}
}

.slider-post-overlay {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgba(0,0,0,0.5);
	  background: -moz-linear-gradient(top,  rgba(0,0,0,0) 10%,rgba(25,15,5,0.5) 90%); /* FF3.6+ */
	  background: -webkit-gradient(linear, left top, left bottom, color-stop(10%,rgba(0,0,0,0)), color-stop(90%,rgba(25,15,5,0.5))); /* Chrome,Safari4+ */
	  background: -webkit-linear-gradient(top,  rgba(0,0,0,0) 10%,rgba(25,15,5,0.5) 90%); /* Chrome10+,Safari5.1+ */
	  background: -o-linear-gradient(top,  rgba(0,0,0,0) 10%,rgba(25,15,5,0.5) 90%); /* Opera 11.10+ */
	  background: -ms-linear-gradient(top,  rgba(0,0,0,0) 10%,rgba(25,15,5,0.5) 90%); /* IE10+ */
	  background: linear-gradient(to bottom, rgba(25,15,5,0) 10%,rgba(25,15,5,0.5) 90%); /* W3C */
	z-index: 1;
}

