
/* ## Single Post Comments */

.comment-content a {
	word-wrap: break-word;
}

.comment-content p {
	font-family: 'Roboto', sans-serif;
	font-weight: 400;
	color: #333;
}

@media screen and (min-width: 50em) {
	.comment-content p {
		font-size: 85%;
		line-height: 1.55em;
	}
}

.bypostauthor {
	display: block;
}

.comments-title {
	border-bottom: 2px solid #f3f2f1;
	font-family: 'Overpass', sans-serif;
	color: #000;
	text-transform: uppercase;
	font-weight: 600;
	font-style: normal;
	text-decoration: none;
	letter-spacing: 0.15em;
	font-size: .65em;
	padding-bottom: 1.5em;
	margin: 3em 0 3em;
}

.comment-list {
	padding: 0;
	margin: 0;
}

.comment-list,
.comment-list ol {
	list-style-type: none;
}

.comment-body {
	border-bottom: 1px solid #ebebeb;
	margin-bottom: 2.2em;
	padding-bottom: .8em;
}

.comment-meta {
	position: relative;
	margin-left: 70px;
	font-family: 'Overpass', sans-serif;
	font-size: 80%;
	color: #000;
	background-color: inherit;
	margin-top: inherit;
	padding: 0;
}

.comment .avatar {
	position: absolute;
	left: -70px;
	width: 50px;
	height: 50px;
}

.children {
	margin-left: 1em;
	padding-left: 0;
}

.comment .avatar {
	top: -5px;
}

@media screen and (min-width: 30em) {
	.children {
		margin-left: 2em;
	}
	.comment-meta {
		margin-left: 80px;
		font-size: 90%;
	}
	.comment .avatar {
		top: 0px;
		left: -90px;
		width: 55px;
		height: 55px;
	}
	.comment-content {
		margin-left: 80px;
	}
}

@media screen and (min-width: 50em) {
	.children {
		margin-left: 3em;
	}
	.comment-meta {
		margin-left: 90px;
	}
	.comment .avatar {
		top: 5px;
		left: -90px;
		width: 55px;
		height: 55px;
	}
	.comment-content {
		margin-left: 90px;
	}
}

.comment-meta b a {
	text-decoration: none;
	color: #333;
}

.comment-meta a {
	text-decoration: none;
	color: #888;
}

.comment-meta a:hover,
.comment-meta a:focus {
	border-bottom: 2px solid #000;
}

.comment-metadata a:hover,
.comment-metadata a:focus {
	border-color: #ebebeb;
}

.comment-author {
	font-size: 1em;
	line-height: 1.3em;
}

.comment-author b {
	font-weight: 500;
}

.says {
	font-size: 80%;
}

.comment-metadata {
	margin-top: .5em;
	padding-bottom: .5em;
	font-size: .8em;
	line-height: 1em;
}

.comment-metadata .edit-link {
	float: right;
	margin-top: -1.5em;
}

.comment-metadata .edit-link a {
	padding: .5em 1em;
	font-weight: 600;
	color: #999;
}

.comment-metadata .edit-link a:hover,
.comment-metadata .edit-link a:focus,
.comment-metadata .edit-link a:active {
	color: #fff;
	border: 1px solid #999;
}

.reply {
	margin-bottom: 1.5em;
}

@media screen and (min-width: 30em) {
	.reply {
		margin-left: 80px;
	}
}

@media screen and (min-width: 50em) {
	.reply {
		margin-left: 90px;
	}
}

.reply a {
	display: inline-block;
	padding: 7px 12px 4px;
	font-family: 'Overpass', sans-serif;
	font-size: .62em;
	color: #fff;
	line-height: 1.6em;
	text-decoration: none;
	transition: all .3s;
	border-radius: 99px;
	background-color: #c99d6e;
	text-transform: uppercase;
	font-weight: 500;
	letter-spacing: 0.15em;
}

.reply a:hover,
.reply a:focus {
	background: #000;
	color: #fff;
	border-color: #000;
}

.comment-respond {
	padding: 1em 1.5em;
	background-color: #fbfbfb;
	border: 1px solid #ebebeb;
	margin-bottom: 2em;
}

@media screen and (min-width: 50em) {
	.comment-respond {
		padding: 1em 1.8em;
	}
}

#reply-title {
	font-weight: 600;
	font-family: 'Overpass', sans-serif;
	font-size: 20px;
	margin-bottom: .5em;
	display: block;
}

.comment-content ol {
	list-style-type: decimal;
}

.comment-content a {
	word-wrap: break-word;
}

.bypostauthor .avatar {
	box-sizing: content-box;

}

.logged-in-as {
	font-family: 'Overpass', sans-serif;
	margin: .5em 0;
}

@media screen and (min-width: 50em) {
	.logged-in-as {
		font-size: 85%;
	}
}

.comment-notes {
	font-family: 'Overpass', sans-serif;
	color: #888;
	margin-top: .5em;
}

.comment-form label {
	font-family: 'Overpass', sans-serif;
	width: 5em;
	display: inline-block;
}

.comment-form textarea,
.comment-form input {
	border-radius: 0;
	border: 1px solid #e3e3e3;
	font-family: 'Roboto', sans-serif;
	font-size: 85%;
}

.comment-form .submit {
	background-color: #000;
	font-family: 'Overpass', sans-serif;
	font-weight: 600;
	border: none;
	color: #fff;
	border-radius: 0;
	padding: 1em 1.5em;
}

.form-submit {
	margin-bottom: .8em;
}

.comment-form .submit:hover,
.comment-form .submit:active,
.comment-form .submit:focus {
	background-color: #c99d6e;
}

.site-main .comment-navigation  {
	margin: 0 0 1.5em;
	overflow: hidden;
}

.comment-navigation .nav-previous {
	float: left;
	width: 100%;
}

.comment-navigation .nav-next, {
	float: right;
	text-align: center;
	width: 100%;
}

.comment-navigation a {
	font-size: 1.5em;
}

.comment-navigation {
	font-family: 'Overpass', sans-serif;
	font-weight: 600;
	text-align: left;
	overflow: hidden;
}

.comment-navigation a {
	color: #000;
	display: block;
	width: 100%;
	text-decoration: none;
	padding: 1.2em;
	border: 1px solid #ebebeb;
	transition: all .3s;
	background-color: #fbfbfb;
}

@media screen and (min-width: 62em) {
	.comment-navigation a {
		padding: 1.5em;
	}
}

@media screen and (min-width: 80em) {
	.comment-navigation a {
		padding: 1.8em;
	}
}

.comment-navigation a:hover,
.comment-navigation a:focus {
	border-color: #999;
	color: #fff;
	background-color: #999;
}

@media screen and (min-width: 30em) {
	.comment-navigation .nav-previous {
		display: flex;
		align-items: stretch;
		float: left;
		width: 48%;
	}

	.comment-navigation .nav-next {
		display: flex;
		align-items: stretch;
		float: right;
		text-align: right;
		width: 48%;
	}
}
