
/*--------------------------------------------------------------
# Page
--------------------------------------------------------------*/

#page,
.single #page,
.archive #page,
.search #page {
	margin-top: 75px;
}

@media screen and (min-width: 62em) {
	#page,
	.single #page,
	.archive #page,
	.search #page {
		margin-top: inherit;
	}
}

.page .content-area .hentry {
	border-bottom: none;
	padding-bottom: 0;
	margin-bottom: 0;
}

.page-content .search-icon {
	display: none;
}

/* ## Page Title */

#page-title {
	background-color: #f7f7f7;
}

#page-title h1 {
	font-family: 'Overpass', sans-serif;
	font-weight: 400;
	letter-spacing: 0.06em;
	text-align: center;
	color: #5f5f5f;
	font-size: .75em;
	padding: 20px 0;
	margin: 0 0 21px;
}

.page #page-title h1 {
	font-size: .9em;
	margin: 18px 0 10px;
}

#page-title h1 span {
	font-style: normal;
	color: #1a1a1a;
	font-weight: 500;
	text-transform: uppercase;
}

@media screen and (min-width: 62em) {
	#page-title h1 {
		font-size: .65em;
		margin-top: 0;
		margin-bottom: 33px;
	}

	.page #page-title h1 {
		font-size: 17px;
		margin-top: 0;
		margin-bottom: 15px;
	}
}

.archive-description p {
	color: #fff;
	margin-bottom: 0;
	font-family: 'Overpass', sans-serif;
}

/* ## Page Thumbnail */

.page-template-default #primary .post-thumb,
.page-template-template-fullwidth #primary .post-thumb {
	margin: 1.5em 0 1em;
}

@media screen and (min-width: 62em) {
	.page-template-default #primary .post-thumb,
	.page-template-template-fullwidth #primary .post-thumb {
		float: inherit;
		margin-right: inherit;
		width: 100%;
	}
}

.archive-year {
	font-weight: bold;

}

.archive-date-link {
	@include breakpoint( small down) {
		margin-bottom: 10px;
	}

	.archive-date {
		font-size: 1rem;
		margin-right: 20px;
	}

	.archive-links {
		font-size: 1rem;
		a {
			color: #d47d22;
			&:hover {
				text-decodario: underline;
			}
		}
	}
}
