.entry-content:after,
.post-thumb:before,
.post-thumb:after,
.post-thumb-mobile:before,
.post-thumb-mobile:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
  content: "";
  display: table;
  table-layout: fixed; }

.clear:after,
.entry-content:after,
.post-thumb:after,
.post-thumb-mobile:after,
.site-main article:after,
#secondary article:after,
footer article:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
