
/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/

#secondary .widget {
	margin-bottom: 30px;
}

#secondary .widget:last-of-type {
	margin-bottom: 0;
}

.widget-title {
	background-color: #000;
	margin-top: 0;
	padding-top: 0;
	color: #fff;
	font-size: 11px;
	text-align: center;
	padding: 18px 0 16px;
	margin-bottom: 30px;
	font-family: 'Roboto', sans-serif;
	text-transform: uppercase;
	font-weight: 400;
	letter-spacing: 0.15em;
}

footer .widget-title {
	background-color: #f0f0f0;
	color: #000;
}

#secondary .widget_archive .widget-title,
#secondary .widget_categories .widget-title,
#secondary .widget_search .widget-title,
#secondary .widget_pages .widget-title,
#secondary .widget_meta .widget-title,
#secondary .widget_recent_entries .widget-title,
#secondary .widget_rss .widget-title,
#secondary .widget_nav_menu .widget-title,
#secondary .widget_tag_cloud .widget-title,
#secondary .widget_recent_comments .widget-title {
	margin-bottom: 10px;
}

.widget ul,
.widget ol {
	padding: 10px 0 0;
	margin: 0;
	font-family: 'Overpass', sans-serif;
	list-style-type: none;
}

.widget_archive li:last-of-type,
.widget_categories li:last-of-type,
.widget_pages li:last-of-type,
.widget_meta li:last-of-type,
.widget_recent_comments li:last-of-type,
.widget_recent_entries li:last-of-type,
.widget_nav_menu li:last-of-type {
	border-bottom: none;
}


.site-content {
	.widget_archive li,
	.widget_categories li,
	.widget_pages li,
	.widget_meta li,
	.widget_recent_comments li,
	.widget_recent_entries li,
	.widget_nav_menu li {
		color: #808080;
		border-bottom: 2px solid #f3f2f1;
		font-size: 15px;
		padding: .5em 0;

		a {
			&:hover {
				color: #000;
			}
		}
	}

	.widget li {
		display: block;
		padding: 10px 0;
	}

}



.widget li ul,
.widget li ol {
	margin-left: .5em;
}

.widget li:last-child {
	padding-bottom: .35em;
	border: none;
}

@media screen and (min-width: 30em) {
	.widget li ul,
	.widget li ol {
		margin-left: 1em;
	}
}

.widget .entry-title {
	font-weight: 500;
	text-decoration: none;
	font-size: .9em;
}

.widget .entry-title:hover,
.widget .entry-title:focus {
	text-decoration: underline;
}


.widget_nav_menu .sub-menu {
	padding: 0;
}

.widget_nav_menu  li ul,
.widget_nav_menu  li ol {
	margin-left: .6em;
}

.site-content .widget_nav_menu .sub-menu li {
	border-bottom: none;
	padding: .25em 0;
}

.widget select {
	font-size: .75em;
	width: 100%;
	padding: 1em .8em;
	margin-top: 1em;
}

.widget_calendar {
	font-family: 'Overpass', sans-serif;
	caption {
		padding: 0 0 .5em 0;
		font-size: .7em;
		font-weight: 500;
		text-align: center;
	}
	thead {
		background: #000;
		color: #fff;
	}

	thead th {
		border-bottom-width: 2px;
	}

	td {
		padding: .2em;
		font-size: .8em;
		text-align: center;
		background: #f7f7f7;
		border: 3px solid white;
	}

	th {
		border: none;
	}
	.pad {
		background: #fff;
	}

}


#wp-calendar {
	margin-bottom: 0;
}

.widget_rss li {
	margin-bottom: 1em;
}
.widget li a.rsswidget {
	padding-right: .5em;
	font-size: 1em;
	line-height: 1.4em;
}

.rss-date,
.widget_rss cite {
	color: #333;
	font-size: .8em;
}

.rssSummary {
	padding: .5em 0;
	font-family: 'Roboto', sans-serif;
	font-size: .8em;
	line-height: 1.4em;
}

.textwidget {
	font-size: .8em;
	line-height: 1.6em;
}

.widget_search .search-submit,
.widget_search .search-form-submit  {
	display: none;
}

.widget_search .search-form input {
	float: inherit;
	margin-bottom: 0;
	width: 100%;
}

.widget_search .search-icon {
	display: none;
}

.widget_search input[type="search"] {
	width: 100%;
	font-size: .7em;
	box-sizing: border-box;
	padding: 1.06em 1em;
	margin-top: 5px;
	border-radius: 0;
}


#secondary .search-form .search-form-submit #searchsubmit {
	font-size: .85em;
	padding: 1.05em;
}

/* ## Custom Widget - About Author */

.about-author-image {
	margin-bottom: 1em;
}

footer .about-author-image {
	margin-bottom: 1.4em;
}

.about-author-widget-text {
	font-family: 'Overpass', sans-serif;
	font-size: .7em;
	line-height: 1.8em;
	padding: 0 .7em;
	color: #434343;
	text-align: center;
}

.about-author-signature {
	text-align: center;
	margin-top: 10px;
	color: #d47d22;
	font-size: 21px;
	font-family: 'Nothing You Could Do', serif;
	line-height: 32px;
	font-weight: 500;
}



/* ## Custom Widget - Widget Categories */

.categories-widget-category {
	position: relative;
	margin-bottom: 30px;
	line-height: 47px;
	background-color: #262626;
	color: #fff;
	font-size: 13px;
	font-family: 'Roboto', sans-serif;
	overflow: hidden;
	letter-spacing: 0.1em;
	font-weight: 500;
	transition: background-color .3s;
}

.categories-widget-category:hover .categories-widget-category-title span {
	background-color: #c99d6e;
	color: #fff;
}

.categories-widget-category:hover {
background-color: #404040;
}

.categories-widget-category-has-image {
background: #454545;
}

.categories-widget-category:last-child {
margin-bottom: 0;
}

.categories-widget-category-image {
	display: table;
	width: 100%;
	height: 240px;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
	position: relative;
}

@media screen and (min-width: 62em) {
	.categories-widget-category-image {
		height: 195px;
	}
}

.categories-widget-category-title {
	height: 100%;
	display: table-cell;
	vertical-align: middle;
	text-align: center;
	span {
		font-size: .8em;
		font-family: 'Roboto', sans-serif;
		font-weight: 500;
		color: #000;
		line-height: 1.5em;
		border-radius: 99px;
		padding: 1.1em 1.8em;
		text-transform: uppercase;
		letter-spacing: .2em;
		background: #ffffff;
		display: inline-block;
		transition: all .3s;
	}
}



.categories-widget-category-count {
	font-weight: 400;
	position: relative;
	float: right;
	font-style: italic;
	padding: 0 25px;
	letter-spacing: 0;
	background: #000;
	transition: padding .3s;
}

.categories-widget-category:hover .categories-widget-category-count {
	padding: 0 30px;
	background-color: #999;
}

.categories-widget-category-link {
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
}

.categories-widget-category a {
	font-weight: 100;
}

/* Subscribe Widget */
.subscribe-widget {
	text-align: center;
	background: #000;
	background-size: cover;
	position: relative;

	.widget-body {
		background: rgba(black, 0.3);
		padding: 40px 15%;
	}

	.widget-title {
		background: none;
		color: #fff;
		font-family: 'Overpass', sans-serif;
		font-size: 2rem;
		line-height: .9em;
		margin-bottom: .4em;
		text-transform: none;
		letter-spacing: normal;
	}

	.subscribe-widget-subtitle {
		font-family: 'Roboto', sans-serif;
		color: #fff;
		font-size: 1rem;
		font-weight: 400;
		line-height: 1.8em;
		margin-bottom: 2em;
	}

	.subscribe-field {
		border: 1px solid rgba(black, 0.8);
	}

	.newsletter_error_message {
		font-size: 0.81rem;
		color: red;
		background: #fff;
	}
}

#secondary {
	.widget-body {
		padding: 30px 10%;
	}

	.subscribe-widget {
		.widget-title {
			font-size: 25px;
		}
	}
	.subscribe-widget-subtitle{
		font-size: 0.8rem;
	}
}

/* End nesletter Widget*/

#instagram-footer {
	position: relative;
	text-align: center;
	margin-bottom: -.3em;
	overflow: hidden;
}

.instagram #sbi_load {
	display: none;
}

#instagram-footer .instagram-pics {
	margin: 0;
	padding: 0;
}

#instagram-footer .instagram-pics li {
	width:33.285714%;
	display:inline;
	float:left;
}

#instagram-footer .instagram-pics li:last-of-type {
	display: none;
}

@media screen and (min-width: 62em) {
	#instagram-footer .instagram-pics li {
		width:14.28571428571429%;
	}
	#instagram-footer .instagram-pics li:last-of-type {
		display: inline;
	}
}

#instagram-footer .instagram-pics li img {
	display: block;
}

#instagram-footer .instagram-pics li img:hover {
	opacity:.7;
}

#instagram-footer h2 {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%,-50%);
	-moz-transform: translate(-50%,-50%);
	-o-transform: translate(-50%,-50%);
	transform: translate(-50%,-50%);
	background: #000;
	color: #fff6f1;
	padding: 1.5em 2.3em;
	z-index: 9;
	border-radius: 99px;
	line-height: 1.6em;
	text-transform: uppercase;
	font-size: .8em;
}

#instagram-footer p.clear {
	display:none;
}

#secondary .instagram-pics {
	overflow:hidden;
}

#secondary .instagram-pics li{
	width: 27%;
	display: inline-block;
	border-bottom: none;
	padding: 0;
	margin: 0 0 15px 15px;
	float:left;
}

#secondary .instagram-pics li img {
	max-width:100%;
	vertical-align:middle;
	transition: all .3s;
}

#secondary .instagram-pics li img:hover{
	opacity:.7;
}

.widget_popper_recent_comments ul,
.widget_popper_recent_posts ul {
	margin-top: 2em;
	margin-bottom: 0;
	padding-bottom: 0;
}
.site-content .widget_popper_recent_comments li,
.site-content .widget_popper_recent_posts li {
	position: relative;
	padding: 0 0 1em 70px;
	margin-bottom: 2em;
	border-bottom: 1px solid #c3c3c3;
}

.widget_popper_recent_comments li:last-child,
.widget_popper_recent_posts li:last-child {
	margin-bottom: 0;
	border-bottom: none;
	padding-bottom: 0;
}

.widget_popper_recent_comments a,
.widget_popper_recent_posts a {
	display: block;
	font-size: 1em;
}

.widget_popper_recent_comments a:hover,
.widget_popper_recent_comments a:focus,
.widget_popper_recent_posts a:hover,
.widget_popper_recent_posts a:focus {
	text-decoration: none;
	border-bottom: none;
}
.widget_popper_recent_comments .post-icon,
.widget_popper_recent_posts .post-icon {
	position: absolute;
	left: 0;
	width: 50px;
	height: 50px;
}

.widget_popper_recent_comments .post-icon img {
	border-radius: 50px;
}

.widget_popper_recent_comments .title,
.widget_popper_recent_comments .original-title{
	margin-top: 0;
	margin-bottom: .8em;
	font-size: .7em;
	line-height: 1.4em;
}

.widget_popper_recent_comments .excerpt {
	margin-top: 0;
	margin-bottom: .8em;
	font-family: 'Overpass', serif;
	font-size: .8em;
	line-height: 1.4em;
}

.widget_popper_recent_comments .excerpt::before {
	content: "“";
}

.widget_popper_recent_comments .excerpt::after {
	content: "”";
}

.widget_popper_recent_comments .original-title {
	color: #808080;
	font-weight: 500;
}

.widget_popper_recent_comments .original-title span {
	font-weight: normal;
	font-style: italic;
}

.widget_popper_recent_comments a:hover .original-title,
.widget_popper_recent_comments a:focus .original-title {
	color: #000;
	text-decoration: underline;
}

.widget_popper_recent_posts .post-icon {
	width: 50px;
	height: 50px;
	font-size: 1.3em;
	line-height: 50px;
	text-align: center;
	color: #fff;
	background: #333333;
}

.widget_popper_recent_posts .title {
	margin: 0;
	font-size: .9em;
	line-height: 1.4em;
}

.widget_popper_recent_posts .meta {
	margin: 0 0 .8em;
	font-size: .7em;
	text-transform: uppercase;
	color: #808080;
}

.widget_popper_recent_posts a:hover .title,
.widget_popper_recent_posts a:focus .title {
	text-decoration: underline;
}


