//* ==========================================================================
//* ===== Menus  ===== */
//*========================================================================== */

#social-mobile {
	 li {
		display: inline-block;
		border-bottom: 0px;
		margin-left: 5px;
		i {
			font-size: 1.125rem;
		}
	}
}



#desktop-site-navigation {
	text-align: center;
	width: 100%;
	float: inherit;
	border-top: 2px solid #f3f2f1;
	border-bottom: 2px solid #f3f2f1;
	padding: 10px 0;
	ul {
		li a {
			font-weight: 600;
		}
	}
}

/* ## Header Navigation */

.main-navigation {
	clear: both;
	display: block;
	float: left;
	width: 100%;
}

.main-navigation .menu-item-description {
	color: #707070;
	color: rgba(51, 51, 51, 0.7);
	font-family: 'Overpass', sans-serif;
	font-size: 12px;
	font-size: 1.2rem;
	font-weight: 400;
	line-height: 1.5;
	margin-top: 0.5em;
}

.no-js .main-navigation ul ul {
	display: block;
}

@media screen and (min-width: 62em) {
	.main-navigation,
	.main-navigation.toggled {
		clear: inherit;
	}
}

.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
}

@media screen and (min-width: 62em) {
	.main-navigation ul {
		display: block;
	}
}

.main-navigation a {
	font-size: 11px;
	letter-spacing: 0.08em;
	display: block;
	font-family: 'Lato', sans-serif;
	font-weight: 400;
	color: #fff;
	text-transform: uppercase;
	padding: 13px 0;
	position: relative;
	text-decoration: none;
	letter-spacing: 3px;
	border-bottom: none;
}

@media screen and (min-width: 62em) {
	.main-navigation a {
		transition: all .3s;
	}
	.main-navigation .current-menu-item a,
	.main-navigation a:hover,
	.main-navigation a:active,
	.main-navigation a:focus {
		font-weight: 400;
	}
	.main-navigation a,
	.main-navigation ul ul li:last-child a {
		border-bottom: none;
	}
	.main-navigation .sub-menu a {
		font-size: 12px;
		letter-spacing: 0.18em;
		padding: 0.8em 0;
		color: #fff;
		line-height: 2em;
	}
	.main-navigation .current-menu-item .sub-menu .current-menu-item a {
		color: #999;
	}
	.main-navigation .current-menu-item .sub-menu a {
		color: #fff;
	}
}

.main-navigation .nav-menu > ul > li:last-child,
.main-navigation .sub-menu > ul > li:last-child,
.main-navigation .nav-menu > li:last-child,
.main-navigation .sub-menu > li:last-child {
	border-bottom: 0;
}

.main-navigation .sub-menu a:hover {
	color: #999;
	transition: all .3s;
}

.main-navigation .sub-menu .current-menu-item > a::after,
.main-navigation .sub-menu .current-menu-ancestor > a::after,
.main-navigation .sub-menu .current_page_item > a:after {
	width: 0%;
}

.main-navigation .sub-menu .current-menu-item > a,
.main-navigation .sub-menu .current_page_item > a {
	color: #999;
}

.main-navigation ul ul {
	display: none;
	margin-left: 0.8em;
}

.main-navigation ul .toggled-on {
	display: block;
}

.main-navigation li {
	position: relative;
	border-bottom: 1px solid #303030;
}

@media screen and (min-width: 62em) {
	.main-navigation li {
		display: inline-block;
		border: none;
		margin-right: 15px;
	}
	.main-navigation li.menu-item-has-children {
	   margin-right: 30px;
	}
	.main-navigation li:last-child {
		margin-right: 0;
	}
}

@media screen and (min-width: 70em) {
	.main-navigation li {
		margin-right: 25px;
	}
	.main-navigation li.menu-item-has-children {
	   margin-right: 45px;
	}
}

@media screen and (min-width: 78em) {
	.main-navigation li {
		margin-right: 30px;
	}
	.main-navigation li.menu-item-has-children {
	   margin-right: 50px;
	}
	.main-navigation li.menu-item-has-children:last-of-type {
	   margin-right: 20px;
	}
}

/* ## Header Dropdown toggle */

.dropdown-toggle {
	-webkit-tap-highlight-color:  rgba(255, 255, 255, 0);
	border: none;
	-webkit-box-sizing: content-box;
	-moz-box-sizing: content-box;
	box-sizing: content-box;
	font-family: 'Material Icons';
	content: "";
	height: 51px;
	border-radius: inherit;
	background-color: inherit;
	padding: 0;
	position: absolute;
	text-transform: lowercase; /* Stop screen readers to read the text as capital letters */
	top: 0;
	right: 0;
	width: 51px;
}

#primary-menu-mobile .dropdown-toggle {
	height: 44px;
}


.dropdown-toggle:focus {
	outline: none;
}
.dropdown-toggle:after {
	color: #fff;
	content: "\E313";
	font-size: 14px;
	line-height: 42px;
	position: relative;
	top: 0;
	left: 1px;
	width: 42px;
}

@media screen and (min-width: 62em) {
	.dropdown-toggle {
		-webkit-tap-highlight-color:  rgba(255, 255, 255, 0);
		top: 6px;
		right: -30px;
		border: none;
		width: 30px;
		height: 30px;
	}
	.main-navigation.toggled ul {
		overflow-y: visible;
	}
	.main-navigation ul ul.toggled-on li.menu-item-has-children  {
		margin-right: 0;
	}

	/* Left align the menu items */
	.main-navigation li li {
		display: block;
		text-align: left;
		margin-right: 0;
		border-bottom: 1px solid #444;
		padding: 0 1.2em;
	}
	.main-navigation li li li {
	}
	/* Position the dropdown menus absolutely */
	.main-navigation ul ul.toggled-on {
		position: absolute;
		width: 18em;
		display: block;
		z-index: 10;
		top: 100%;
		margin-left: 0;
		border: 1px solid #1a1b1d;
		background: #1a1b1d;
		padding: .5em 0;
	}
	.main-navigation ul ul ul.toggled-on {
		position: absolute;
		top: -1px;
		left: 100%;
	}
	/* Indent 3rd level dropdown menus */
	.main-navigation ul ul ul a {
		padding-left: 2em;
	}
	.sub-menu .dropdown-toggle {
		top: 6px;
		right: 0;
	}
	.header-1 .sub-menu .dropdown-toggle {
		top: -5px;
	}
	.dropdown-toggle:after {
		line-height: 27px;
		color: #fff;
	}
}
.sub-menu .dropdown-toggle {
	height: 38px;
	width: 61px;
}


@media screen and (min-width: 62em) {
	#site-navigation {
		display: none;
	}
}

.menu-toggle, .main-navigation.toggled ul {
	display: block;
}

.menu-toggle {
	-webkit-tap-highlight-color:  rgba(255, 255, 255, 0);
	position: absolute;
	font-family: 'Material Icons';
	content: "";
	top: 0;
	right: 0;
	background-color: transparent;
	color: #999;
	font-size: 1.5em;
	padding: 20px;
	border: inherit;
	z-index: 99;
	opacity: 0;
	transition: all .3s;

}

.toggled .menu-toggle {
	z-index: 100;
}

.admin-bar .toggled .menu-toggle {
}

.menu-toggle:after {
	content: "\E5D2";
}

.toggled .menu-toggle:after {
	content: "\E5CD";

}

.menu-toggle:focus, .search-toggle:focus, .search-toggle-active:focus {
	outline: 0;
}

@media screen and (min-width: 62em) {
	.menu-toggle {
		display: none;
	}
}

/* ## Header Hovering Menu */

.main-navigation.toggled {
	left: -.0em;
	position: fixed;
	top: 0;
	z-index: 1;
}

.main-navigation.toggled ul {
	max-height: calc(100vh);
	overflow-y: auto;
}

.main-navigation.toggled ul ul {
	display: none;
}

.main-navigation.toggled ul ul.toggled-on,
.main-navigation.toggled ul ul.toggled-on > ul {
	display: block;
}

.nav-wrapper {
	background-color: #000;
	position: fixed;
	width: 100%;
	padding: .7em 1.5em 1.5em 1.5em;
	min-height: calc(100vh + 4em);
	opacity: 0;
	top: 90px;
	left: -30em;

	@include breakpoint( small down) {
		top: 60px;
	}

	#primary-menu-mobile a {
		font-size: 12px;
	}
}

.admin-bar .nav-wrapper {
	top: 84px;
	@include breakpoint( small down) {
		top: 70px;
	}
}
